import React, { Fragment, useContext, useState, useEffect } from "react";
import Modal from "../UI/Modal";
import classes from "./Cart.module.css";
import CartContext from "../../store/cart-context";
import CartItem from "./CartItem";
import Checkout from "./Checkout";
import { init, send } from 'emailjs-com';

const Cart = (props) => {
	const cartCtx = useContext(CartContext);
	const [isSubmitting, setIsSubmitting] = useState(false);
	const [didSubmit, setDidSubmit] = useState(false);
	const [checkout, setChekcout] = useState(false);

	const totalAmount = `${cartCtx.totalAmount.toLocaleString('ko-KR')}원`;
	const hasItems = cartCtx.items.length > 0;

	const cartItemRemoverHandler = (id) => {
		cartCtx.removeItem(id);
	};

	const cartItemAddHander = (item) => {
		cartCtx.addItem({...item, amount: 1})
	};

	const orderHandler = () => {
		setChekcout(true);
	};

	useEffect(() => {
		init('user_9RKBAxKZS3dEoBkYVYEt1');
	}, []);

	const submitOrderHandler = async (userData) => {
		setIsSubmitting(true);
		//no error handling. if u want to, see AvailableMeals.js file.
		await fetch('https://react-http-3cca3-default-rtdb.firebaseio.com/orders.json', {
		method: 'POST',
			body: JSON.stringify({
				user: userData,
				orderedItmes: cartCtx.items
			})
		});
		setIsSubmitting(false);
		setDidSubmit(true);
		cartCtx.clearCart();
		//below email
		let forMail = {};
		let str = '';
		let price = 0;
		for(let i in cartCtx.items) {
			forMail[i] = {
				name: cartCtx.items[i].name,
				amount: cartCtx.items[i].amount,
			}
			price += +(cartCtx.items[i].price);
		}
		for (let i in forMail) {
			str += '<br>';
			str += Object.entries(forMail[i]).map(x=>x.join(': ')).join('&nbsp;');
			str += '</br>';
		}
		str = str.replace(/name:/gi, '');
		str = str.replace(/amount/gi, '개수');
	
		const templete = {
			to_name: userData.email,
			message: str,
			bill: "총액: " + price.toString(),
		}
		send('service_tr0yfqy', 'template_gfuwgnc', templete);
	};

	const modalActions = (
		<div className={classes.actions}>
			<button className={classes.button} onClick={props.onClose}>
				닫기
			</button>
			{hasItems && (
				<button className={classes.button} onClick={orderHandler}>
					주문
				</button>
			)}
		</div>
	);
	
	const cartItems = (
		<ul className={classes["cart-items"]}>
			{cartCtx.items.map((item) => (
				<CartItem
				key={item.id}
				name={item.name}
				amount={item.amount}
				price={item.price}
				onRemove={cartItemRemoverHandler.bind(null, item.id)}
				onAdd={cartItemAddHander.bind(null, item)}
				/>
				))}
		</ul>
	);
	
	const cartModalContetnt = (
		<Fragment>
			{cartItems}
			<div className={classes.total}>
				<span>합계</span>
				<span>{totalAmount}</span>
			</div>
			{checkout && (
				<Checkout onConfirm={submitOrderHandler} onCancel={props.onClose} />
			)}
			{!checkout && modalActions}
		</Fragment>
	);
	const isSubmittingModalContetnt = <p>연결중...</p>;
	
	const didSubmitModelContetnt = (
		<Fragment>
			<p>완료! 이메일을 확인해주세요.</p>
			<div className={classes.actions}>
			<button className={classes.button} onClick={props.onClose}>
				닫기
			</button>
		</div>
		</Fragment>
	);

	return (
		<Modal onClose={props.onClose}>
			{!isSubmitting && !didSubmit && cartModalContetnt}
			{isSubmitting && isSubmittingModalContetnt}
			{!isSubmitting && didSubmit && didSubmitModelContetnt}
		</Modal>
	);
};

export default Cart;
