import React, { useState, useRef } from "react";
import classes from "./Checkout.module.css";
import PopupDom from "./SearchAdd/PopupDom";
import PopupPostCode from "./SearchAdd/PopupPostCode";

const isEmpty = value => value.trim() === '';

const Checkout = (props) => {
	const detailAddressRef = useRef();
	const addressRef = useRef();
	const emailRef = useRef();

	const [formInputValidity, setFormInputValidity] = useState({email: true, address: true, detailAddress: true});
	const [popupOpen, setPopupOpen] = useState(false);
	const [address, setAddress] = useState('');

	const openPostCode = () => { setPopupOpen(true); };
	const closePostCode = () => { setPopupOpen(false); };

	const detailAddressChangeHandler = () => {
		const detailAddressValue = detailAddressRef.current.value;
		const speical_pattern = /[`~!@#$%^&*|\\'";:?]/gi;

		const detailAddressValueIsValid =
		!isEmpty(detailAddressValue) &&
		!speical_pattern.test(detailAddressValue);

		setFormInputValidity((prevState) => {
			return {...prevState, detailAddress: detailAddressValueIsValid }
		});
		return detailAddressValueIsValid;
	};

	const emailChangeHandler = () => {
		const check = /^([0-9a-zA-Z_.-]+)@([0-9a-zA-Z_-]+)(\.[0-9a-zA-Z_-]+){1,2}$/;
		const emailIsValid = check.test(emailRef.current.value) && !isEmpty(emailRef.current.value);

		setFormInputValidity((prevState) => {
				return {...prevState, email: emailIsValid, };
		});
		if (emailIsValid)
			return true;
		return false;
	};

	const confirmHandler = (event) => {
		event.preventDefault();
		const detailAddCheck = detailAddressChangeHandler();
		const emailCheck = emailChangeHandler();
		let AddCheck = true;
		if (address === "") {
			AddCheck = false;
			setFormInputValidity((prevState) => {
				return { ...prevState, address: false };
			});
		}
		if (!AddCheck || !detailAddCheck || !emailCheck)
			return;
		props.onConfirm({
			email: emailRef.current.value,
			address: addressRef.current.value,
			detailAddress: detailAddressRef.current.value,
		});
	};
	
	const addressResult = (value) => {
		setAddress(value);
		const addressValue = addressRef.current.value;
		const addressValueIsValid = !isEmpty(addressValue);

		setFormInputValidity((prevState) => {
			return { ...prevState, address: addressValueIsValid }
		});
	};

	const addressClasses = `${classes.control2} ${formInputValidity.address ? '' : classes.invalid}`;
	const detailAddressClasses = `${classes.control} ${formInputValidity.detailAddress ? '' : classes.invalid}`;
	const emailClasses = `${classes.control} ${formInputValidity.email ? '' : classes.invalid}`;
	return (
		<React.Fragment>
			<form onSubmit={confirmHandler} className={classes.form}>
				<div className={emailClasses}>
					<label htmlFor="email">이메일</label>
					<input type="text" id="email" ref={emailRef} onChange={emailChangeHandler}/>
				</div>
				<div className={addressClasses}>
					<label htmlFor="street">주소</label>
					<input className={classes.speical_input} type="text" id="street" value={address} ref={addressRef} disabled />
					<button type="button" onClick={openPostCode} className={classes.mine}>
						검색
					</button>
				</div>
				<div className={detailAddressClasses}>
					<label htmlFor="postal">상세주소</label>
					<input type="text" id="postal" ref={detailAddressRef} maxLength='15' onChange={detailAddressChangeHandler}/>
				</div>
				<div className={classes.actions}>
					<button type="button" onClick={props.onCancel}>
						취소
					</button>
					<button>확인</button>
				</div>
			</form>
			<div id="popupDom">
				{popupOpen && (
					<PopupDom>
						<PopupPostCode result={addressResult} onClose={closePostCode} />
					</PopupDom>
				)}
			</div>
		</React.Fragment>
	);
};

export default Checkout;
